import React, { Component } from 'react';
import axios from 'axios';
import GameResults from './GameResults';

class GameSearch extends Component {
	constructor(props) {
		super(props);
		this.state = {
			display: [],
		}

	}
	componentDidMount = () =>{
		axios.get('https://gamers-unite-project.herokuapp.com/api/v1/games')
		.then(result =>{
			this.setState({display: result.data})
		})
	}
	// ### CANT GET THIS TO WORK IN PLACE OF INLINE JSX SO I CAN PASS IN USER ID TO EACH GameResults component, to let each component know the user that is favoriting hte game
	// displayGames = () =>{
	// 	this.state.display.map(function(object){
	// 		return (
	// 			<div>
	// 			{console.log(object._id)}
	// 			<p>{object._id}</p>
	// 			<GameResults key={object._id} data={object} />
	// 			</div>
	// 		)
	// 	})
	// }
	render() {
		while(!this.props.user) {
			return <div>Loading...</div>
		}
		return (
			<div>
				{this.state.display.map(function(object){
					return (
						<GameResults key={object._id} data={object} />
					)
				})}
			</div>
		)
	}
}

export default GameSearch;



