import React, { Component } from 'react';
import axios from 'axios';

class GameResults extends Component {
	constructor(props){
		super(props);
		this.state={
			hello: 'world'
		}
	}
	// I NEED TO GIVE THE CURRENT USER ID TO THIS POST REQUEST SO I CAN USE IT IN BACK END CALL TO UPDATE CURRENT USER
	getGame = () =>{
		axios.post('https://gamers-unite-project.herokuapp.com/api/v1/games/'+this.props.data._id)
		.then(result =>{
			console.log(result)
		})
	}
	render() {
		return(
			<div className='games'>
				<a onClick={this.getGame}>{this.props.data.name}</a>
				<div>{this.props.data.platform}</div>
			</div>
		)
	}
}
export default GameResults;


